import "./App.css";
// import ContactInfoBar from "./components/ContactInfoBar";
import MainBody from "./components/mainbody/MainBody";
import NavBar from "./components/navbar/NavBar";
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router basename="/main">
        <Route render={() => <NavBar />}></Route>
        <Switch>
          <Route
            exact
            path="/"
            render={() => <Redirect to="/homepage"></Redirect>}
          ></Route>

          <Route
            path="/resume"
            render={() => <MainBody displayContent="resume" />}
          ></Route>

          <Route
            path="/project"
            render={() => <MainBody displayContent="project" />}
          ></Route>

          <Route
            path="/contact"
            render={() => <MainBody displayContent="contact" />}
          ></Route>

          <Route render={() => <MainBody displayContent="homepage" />}></Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
